import Image from "next/image";
import img1 from "../../../public/Senior businessman portrait on white background _ Premium AI-generated image.jpeg";

const Testimonial = () => {
  return (
    <div className="text-center mt-12">
      {/* Title with a rounded button-like design */}
      <div className="mb-4">
        <h1 className="text-white border text-center rounded-full border-white py-2 px-8 inline-block">
          Testimonials
        </h1>
      </div>

      {/* Clients feedback heading */}
      <h2 className="text-white text-center text-4xl font-bold mt-8">
        Clients Feedback
      </h2>

      <p className="text-[#CBCBCB] w-[550px] mx-auto text-center mt-6">
        We are trusted by 10,000+ global customer teams who have worked on
        projects using this template.
      </p>

      {/* Review Cards Section */}
      <div className="flex justify-center space-x-8 mt-12">
        {/* Card 1 */}
        <div className="bg-[#0F0F0F] p-6 rounded-lg shadow-lg w-[394px] h-[320px]">
          <div className="mb-4">
            {/* Image */}
            <div className="w-20 h-20 bg-gray-300 rounded-full mb-4 text-left mx-auto">
              <Image
                src={img1} // Random image
                alt="John's image"
                width={80} // width of the image
                height={80} // height of the image
                className="object-cover rounded-full"
              />
            </div>
            <h1 className="text-lg font-semibold text-gray-800 mb-2 text-left">John</h1>
            {/* Testimonial text */}
            <p className="text-gray-600 mb-4 text-left">
              "This template made our work so much easier. The team is very
              responsive and the product is top-notch."
            </p>
            {/* Position */}
            <p className="text-sm text-gray-500 text-left">CEO, Example Corp</p>
          </div>
        </div>

        {/* Card 2 */}
        <div className="bg-[#0F0F0F] p-6 rounded-lg shadow-lg w-[394px] h-[320px]">
          <div className="mb-4">
            {/* Image */}
            <div className="w-20 h-20 bg-gray-300 rounded-full mb-4 text-left mx-auto">
              <Image
                src="https://picsum.photos/200?random=2" // Random image
                alt="Jane's image"
                width={80} // width of the image
                height={80} // height of the image
                className="object-cover rounded-full"
              />
            </div>
            {/* Title */}
            <h3 className="text-lg font-semibold text-gray-800 mb-2 text-left">
              Jane Smith
            </h3>
            {/* Testimonial text */}
            <p className="text-gray-600 mb-4 text-left">
              "The flexibility and ease of use are amazing. We are able to
              deliver our projects faster than ever."
            </p>
            {/* Position */}
            <p className="text-sm text-gray-500 text-left">
              Marketing Head, XYZ Ltd.
            </p>
          </div>
        </div>

        {/* Card 3 */}
        <div className="bg-[#0F0F0F] p-6 rounded-lg shadow-lg w-[394px] h-[320px]">
          <div className="mb-4">
            {/* Image */}
            <div className="w-20 h-20 bg-gray-300 rounded-full mb-4 text-left mx-auto">
              <Image
                src="https://picsum.photos/200?random=3" // Random image
                alt="Samuel's image"
                width={80} // width of the image
                height={80} // height of the image
                className="object-cover rounded-full"
              />
            </div>
            {/* Title */}
            <h3 className="text-lg font-semibold text-gray-800 mb-2 text-left">
              Samuel Lee
            </h3>
            {/* Testimonial text */}
            <p className="text-gray-600 mb-4 text-left">
              "An amazing experience! The customer support team was incredible,
              and we couldn't be happier with the results."
            </p>
            {/* Position */}
            <p className="text-sm text-gray-500 text-left">
              Project Manager, ABC Co.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Testimonial;
